@import url(./fonts/ms-sans-serif.css);

html, body {
   padding: 0;
   margin: 0;
   overflow: hidden;
}

#head {
   font-family: "MS Sans Serif";
   text-align: center;
   background-color: #444;
}
#head h1 {
   color: #fff;
   font-size: 1.5rem;
   margin: 0;
}
#head h3 {
   color: #eee;
   font-size: 1.1rem;
   font-weight: normal;
   margin: 0.5rem 0;
}
#head .button-container {
   background-color: #222;
   padding: 0.2rem 0;
}

.button-container {
   display: flex;
   justify-content: center;
}
.button-container .button {
   margin: 5px;
}
.button {
   font-family: "MS Sans Serif";
   display: table;
   cursor: pointer;
}
.button:hover {
   background-color: #d5d5d5 !important;
}
.button:active {
   background-color: #c5c5c5 !important;
}
.ct-xp .button {
   background-color: #ccc;
   border: 3px solid;
   border-top-color: #eee;
   border-left-color: #eee;
   border-right-color: #666;
   border-bottom-color: #666;
}
.ct-95 .button {
   min-width: 5rem;
   text-align: center;
   background-color: #ccc;
   border: 2px solid;
   border-radius: 1px;
   border-top-color: #fff;
   border-left-color: #fff;
   border-right-color: #666;
   border-bottom-color: #666;
   padding: 0.1rem 0.5rem;
   box-shadow: 0 0 0 2px #000;
}